
.scroll-wrapper {
  height: 100%;
  overflow: hidden;
}
.scroll-loading {
  width: 20px;
  height: 20px;
}
.pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: all;
  transition: all;
}
.pulldown-wrapper .after-trigger {
  margin-top: 10px;
}
.pullup-wrapper {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 16px 0;
}



